.donate {
  min-height: 900px;
  margin-top: 20px;
  @media(max-width: 767px){
    min-height: unset;
  }
  // .donate__container
  &__container {
  }
  // .donate__title-block
  &__title-block {
    display: flex;
    align-items: center;
    margin-bottom: 20px;
  }
  // .donate__block
  &__block {
    margin-left: 10px;
    margin-right: 20px;
  }
  // .donate__title
  &__title {
  }
  // .donate__items
  &__items {
    display: none;
    grid-template-columns: repeat(auto-fill,minmax(260px,1fr));
    grid-template-rows: repeat(auto-fill,minmax(221px,1fr));
    gap: 20px;
    font-size: rem(20);
    margin-top: 50px;
    &.active{
      display: grid;
      margin: 0;
    }
  }
  // .donate__item
  &__item {
    display: block;
    padding: 20px;
    border-radius: 25px;
    background-color: var(--orange);
  }
  // .donate__item-price
  &__item-price {
    color: var(--black);
    padding: 10px 30px;
    border-radius: var(--bradius);
    width: fit-content;
    background-color: var(--white);
    margin-bottom: 10px;
  }
  // .donate__item-content
  &__item-content {
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    gap: 10px;
  }
  // .donate__item-image
  &__item-image {
    flex-basis: 60%;
    margin-left: 10px;
    text-transform: uppercase;
    &>img{
      width: 92px;
      height: 93px;
    }
  }
  // .donate__item-title
  &__item-title {
    margin-top: 10px;
    margin-left: 5px;
  }
  // .donate__item-cart
  &__item-cart {
    width: 55px;
    height: 55px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: var(--bradius);
    background-color: var(--white);
  }
}
.goods{
  &__range-block{
    position: relative;
  }
  &__range{
    appearance: none;
    width: 100%;
    height: 8px;
    padding: 0;
    border-radius: 10px;
    margin-bottom: 15px;
    background: #cdcdcd;
    &::-webkit-slider-thumb{
      appearance: none;
      width: 20px;
      height: 20px;
      border-radius: 50%;
      background-color: var(--orange);
      cursor: pointer;
    }
  }
  &__items{
    grid-template-columns: 1fr;
    grid-template-rows: repeat(auto-fill,minmax(221px,1fr));
  }
  &__item{
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-around;
    padding: 20px;
    font-size: rem(18);
    gap: 30px;
    background-color: rgba($color: #000, $alpha: 0.5);
    border-radius: var(--bradius);
    &-subtitle{
      font-size: rem(16);
      color: #8a8a8a;
    }
    &-coin{
      display: flex;
      justify-content: center;
      align-items: center;
      background-color: var(--orange);
      border-radius: 35px;
      width: 150px;
      height: 150px;
    }
    &-btn{
      display: flex;
      align-items: center;
      background-color: var(--orange);
      padding: 20px 30px;
      text-transform: uppercase;
      border-radius: var(--bradius);
      &-block{
        display: flex;
        justify-content: center;
        align-items: center;
        color: var(--orange);
        background-color: var(--white);
        width: 50px;
        height: 50px;
        border-radius: 10px;
        margin-left: 15px;
      }
    }
    &-calc{
      display: flex;
      justify-content: space-between;
      text-align: center;
      gap: 10px;
    }
    &-take>span,
    &-give>span{
      color: var(--orange);
    }
  }
}
.tab__content1{
  width: 100%;
  margin-top: 20px;
  &>*{
    display: none;
    &.active{
      display: block;
    }
  }
}
